.radioCustomButton {
    position: absolute;
    width: auto;
    opacity: 0;
}

.radioCustomButton,
.radioCustomLabel {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}

.radioCustomLabel {
    position: relative;
    width: 100%;
    padding: 30px;
    height: 100%;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    text-align: left;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    font-size: 15px;
    line-height: 1.4;
    letter-spacing: 0;
    border: 1px solid #e6e6e6;
    cursor: pointer;
    -webkit-transition-delay: .4s;
    transition-delay: .4s;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    color: #474747;
}
.radioCustomLabel span:first-letter{
    text-transform: uppercase;
}

@media (max-width: 767px) {
    .radioCustomLabel {
        display: flex;
        flex-direction: row;
        padding: 10px;
        align-items: center;
        -webkit-box-shadow: 0 20px 18px rgb(0 0 0 / 5%);
        box-shadow: 0 20px 18px rgb(0 0 0 / 5%);
        border: 1px solid #fff;
        background-color: #fff;
    }
}


.example-enter {
    opacity: 0.01;
}

.example-enter.example-enter-active {
    opacity: 1;
    transition: opacity 200ms;
}

.example-leave {
    opacity: 1;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 0ms;
}

.example-leave.example-leave-active {
    opacity: 0;
    top: 0;
    left: 0;
    display: none;
    position: absolute;
    transition: opacity 200ms;
    transition-delay: .3s;
}

.answerOptions {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    text-align: left;
}

@media screen and (max-width: 767px) {
    .answerOptions {
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }
}

.answerOption {
    min-height: 325px;
    width: 47.5%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    transition: .3s;
}
.answerOption.disable{
    pointer-events: none;
    width: 100%;
    height: auto;
}

.answerOption.disable .radioCustomLabel img{
    filter: none;
}

@media screen and (max-width: 767px) {
    .answerOption {
        width: 100%;
        min-height: 0;
    }
    .answerOption:first-child{
        margin-bottom: 20px;
    }
}

.answerOption:hover .radioCustomLabel {
    -webkit-box-shadow: 0 20px 18px rgb(0 0 0 / 5%);
    box-shadow: 0 20px 18px rgb(0 0 0 / 5%);
    border: 1px solid #fff;
    background-color: #fff;
}

.answerOption img {
    max-width: 150px;
    display: block;
    margin: 0 auto;
    -webkit-filter: grayscale(60%);
    filter: grayscale(60%);
}

@media screen and (max-width: 767px) {
    .answerOption img {
        order: -1;
        filter: none;
        width: 30%;
        margin-right: 10px;
    }
}

.answerOption:hover img {
    -webkit-filter: none !important;
    filter: none !important;
}

