.quizWrapper {
    -webkit-box-shadow: 0 20px 18px rgb(0 0 0 / 5%);
    box-shadow: 0 20px 18px rgb(0 0 0 / 5%);
    position: relative;
    max-width: 630px;
    background-color: #f9f9f9;
    margin: 30px auto;
    padding: 20px 30px 40px 30px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    text-align: left;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-flex: 0;
    -ms-flex-positive: 0;
    flex-grow: 0;
    width: 100%;
    max-width: 700px;
    color: #474747;
    z-index: 1;
    -webkit-transition: .4s cubic-bezier(.2, 1, .3, 1);
    transition: .4s cubic-bezier(.2, 1, .3, 1);
}

.quizWrapper.final{
