.nextbutton {
    width: 100%;
    height: 70px;
    line-height: 70px;
    position: absolute;
    top: 100%;
    left: 0;
    letter-spacing: 1px;
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    background: #f2c500;
    color: #000000;
    outline: none;
    cursor: pointer;
    border: 1px solid #f2c500;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.nextbutton svg {
    margin-left: 10px;
    stroke: #000000;
    -webkit-transition: .4s cubic-bezier(.2, 1, .3, 1);
    transition: .4s cubic-bezier(.2, 1, .3, 1);
    height: 20px;
}

.nextbutton:hover {
    -webkit-transition: .4s cubic-bezier(.2, 1, .3, 1);
    transition: .4s cubic-bezier(.2, 1, .3, 1);
    background: #f2c500;
    border: 1px solid #f2c500;
}

.nextbutton:hover svg {
    -webkit-transform: translateX(5px);
    transform: translateX(5px);
    -webkit-transition: .4s cubic-bezier(.2, 1, .3, 1);
    transition: .4s cubic-bezier(.2, 1, .3, 1);
}

